import React from 'react';
import PropTypes from 'prop-types';
import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Div,
	View,
	Group,
	Radio,
	Form,
	Card,
	Root,
	InfoRow,
	List,
	Cell,
	Link,
	PullToRefresh,
	Button,
	SelectMimicry,
	FormLayout,
	Input,
	Textarea,
	ScreenSpinner
} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Done from '@vkontakte/icons/dist/24/done';


const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
		igo:0,
		bookmark:0,
		city:[],
        rr:"",
		}


	}
	componentDidMount() {
		const {your_city,city}=this.props;
		this.setState({your_city:your_city,city:city})
	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	ret2(item,i){

		return(
			<Cell
				onClick={()=>{

					this.setState({activePanel: "home",

					})



				}}
				data-to="home"
				asideContent={this.state.your_city === item ? <Icon24Done fill="var(--accent)" /> : null}
			>
				{item}
			</Cell>

		);
	}
	getf(v){
	    this.setState({rr:v})
    }
	render() {

		const {go}=this.props;

		return (


			<Panel id="edit">
				<PanelHeader
					left={<HeaderButton onClick={go} data-to="full">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
					{this.state.rr}
				</PanelHeader>

				<Group>
					<FormLayout><Input top="Заголовок"></Input></FormLayout>
				</Group>
				<Group>
				<FormLayout>
					<Textarea top="Примечание" placeholder="Место, подробности" />
				</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<SelectMimicry
							top="Город"
							data-to="other"
							placeholder="Не выбран"
							onClick={this.go}
						>{this.state.your_city}</SelectMimicry>
					</FormLayout>
					<FormLayout><Input top="Улица"></Input></FormLayout>
				</Group>


				<Group>
					<FormLayout>
							<Input type="date" top="Дата начала"/>
							<Input type="time" top="Время начала"/>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<Input type="date" top="Дата окончания"/>
						<Input type="time" top="Время окончания"/>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<SelectMimicry
							top="Тип"
							data-to="other"
							placeholder="Не выбран"
							onClick={this.go}
						>{this.state.your_city}</SelectMimicry>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout><Input top="Ссылка на группу"></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout><Input top="Ссылка на беседу"></Input></FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Показывать организатора">
							<Radio name="radio" value="1"  defaultChecked>Да</Radio>
							<Radio name="radio" value="2">Нет</Radio>

						</div>
					</FormLayout>
				</Group>
				<Group>
					<FormLayout>
						<div top="Согласованность с администрацией города" onChange={this.getf.bind(this)}>
							<Radio name="radio" value="1"  defaultChecked>Не согласовано</Radio>
							<Radio name="radio" value="2">Не требуется</Radio>
							<Radio name="radio" value="2">Согласовано</Radio>

						</div>
					</FormLayout>
				</Group>
			</Panel>

		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


